* {
  margin: 0px;
}
@media only screen and (min-width: 820px) {
  .navbar {
    display: flex;
    align-items: center;
    justify-content: space-between;
    letter-spacing: 1px;
    padding: 20px;
  }
  .navbar__logo {
    margin-left: 40px;
    cursor: pointer;
  }
  .navbar__logo h2 {
    margin: 0px;
    padding: 0px;
    font-size: 28px;
    font-weight: 600;
  }
  .navbar__links {
    display: flex;
    font-size: 18px;
    font-weight: 600;
    letter-spacing: 1px;
    cursor: pointer;
  }
  .navbar__links > div {
    margin: 0px 30px;
  }
  .navbar__search {
    display: flex;
    width: 200px;
    align-items: center;
    background-color: rgb(235, 232, 232);
    border-radius: 15px;
    padding: 8px 0px;
    margin-right: 40px;
  }
  .navbar__search input {
    background: none;
    border: none;
    outline: none;
  }
  .navbar__search input::placeholder {
    color: rgb(175, 174, 174);
  }
  .navbar__search__icon {
    color: rgb(175, 174, 174);
    font-size: 18px;
    margin-right: 8px;
    margin-left: 8px;
  }
  .navbar-mob {
    display: none;
  }
  .navbar__dropdown {
    display: none;
  }
}

@media only screen and (max-width: 820px) {
  .navbar {
    display: none;
    background-color: red;
  }
  .navbar-mob {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 15px;
    padding-top: 22px;
    cursor: pointer;
  }
  .navbar-mob__burger {
    margin-left: 15px;
    font-size: 30px;
  }
  .navbar-mob__search {
    margin-right: 15px;
    font-size: 30px;
  }
  .navbar-mob h2 {
    font-size: 35px;
  }
  .navbar__dropdown {
    flex-direction: column;
    align-items: center;
    padding: 0px 0px;
  }
  .dropdown__links {
    margin: 5px 0px;
    font-size: 18px;
    color: gray;
    cursor: pointer;
  }
}
@media (min-width: 821px) and (max-width: 1000px) {
  .navbar-mob {
    display: none;
  }
  .navbar__dropdown {
    display: none;
  }
  .navbar__links > div {
    margin: 0px 10px;
  }
  .navbar__burder {
    margin-left: 8px;
  }
  .navbar__search {
    margin-right: 8px;
  }
}
