.header {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  padding-top: 9px;
  margin-top: 20px;
}
.header > div {
  margin: 15px 0px;
}
.header__title {
  font-size: 28px;
  letter-spacing: 1.8px;
}
.header__title h1 span {
  color: rgb(33, 33, 172);
}
.header__article {
  color: gray;
  font-size: 20px;
  word-spacing: 1px;
}
.header__btn button {
  background-color: rgb(30, 30, 155);
  border: none;
  padding: 15px 25px;
  color: white;
  font-size: 15px;
  border-radius: 8px;
  cursor: pointer;
}

@media only screen and (max-width: 820px) {
  .header__title {
    font-size: 19px;
  }
  .header__article {
    font-size: 18px;
  }
  .header {
    margin-top: 0px;
  }
}
@media (max-width: 437px) {
  .header__title {
    font-size: 16px;
  }
  .header__article {
    font-size: 13px;
  }
  .header__btn button {
    padding: 12px 20px;
  }
}
