@media only screen and (min-width: 700px) {
  .section {
    display: flex;
    align-items: center;
    margin-top: 30px;
    padding: 30px;
  }
  .section__left {
    flex: 0.6;
    border-radius: 10px;
  }
  .section__right {
    flex: 0.4;
    margin-top: -30px;
  }
  .section__left img {
    height: 400px;
    width: 100%;
    border-radius: 15px;
  }
  .section__right__box {
    border: 1px solid #ddd;
    padding: 20px;
    box-shadow: 3px 3px 7px rgb(202, 201, 201);
    border-radius: 15px;
    position: relative;
    left: -40px;
    z-index: 100;
    background-color: white;
  }
  .section__right__box h5 {
    color: rgb(30, 30, 155);
    margin: 10px;
    margin-left: 15px;
  }
  .section__right__box h3 {
    font-size: 24px;
    margin-left: 15px;
    letter-spacing: 1px;
  }
  .section__right__box p {
    color: rgb(167, 165, 165);
    margin: 10px;
    margin-left: 15px;
    letter-spacing: 0.8px;
  }
  .section__right__social {
    text-align: center;
    margin-top: 15px;
    margin-left: -20px;
  }
  .social__icon {
    margin: 14px;
    color: rgb(124, 123, 123);
    cursor: pointer;
  }
}

@media only screen and (max-width: 820px) {
  .section__left {
    margin: 23px auto;
  }
  .section__left img {
    height: 300px;
    width: 100%;
    border-radius: 15px;
  }
  .section__right__box {
    border: 1px solid #ddd;
    padding: 20px;
    box-shadow: 3px 3px 7px rgb(202, 201, 201);
    border-radius: 15px;
    margin: 0px auto;
    margin-top: -70px;
    position: relative;
    z-index: 100;
    width: 75%;
    background-color: white;
  }
  .section__right__box h5 {
    color: rgb(30, 30, 155);
    margin: 10px;
    margin-left: 15px;
  }
  .section__right__box h3 {
    font-size: 24px;
    margin-left: 15px;
    letter-spacing: 1px;
  }
  .section__right__box p {
    color: rgb(167, 165, 165);
    margin: 10px;
    margin-left: 15px;
    letter-spacing: 0.8px;
  }
  .section__right__social {
    text-align: center;
    margin-top: 40px;
  }
  .social__icon {
    color: gray;
    margin: 0px 15px;
  }
}
@media only screen and (max-width: 820px) and (min-width: 699px) {
  .social__icon {
    margin: 7px;
  }
  .section__right__social {
    margin-top: 28px;
  }
  .section__right__box {
    border: 1px solid #ddd;
    padding: 20px;
    box-shadow: 3px 3px 7px rgb(202, 201, 201);
    border-radius: 15px;
    margin: 0px auto;
    margin-top: 40px;
    position: relative;
    z-index: 100;
    width: 100%;
    background-color: white;
  }
  .section__left img {
    width: 100%;
    height: 380px;
  }
}
@media (min-width: 572px) and (max-width: 699px) {
  .section__right__box {
    border: 1px solid #ddd;
    padding: 20px;
    box-shadow: 3px 3px 7px rgb(202, 201, 201);
    border-radius: 15px;
    margin: 0px auto;
    margin-top: 40px;
    position: relative;
    top: -90px;
    z-index: 100;
    width: 80%;
    background-color: white;
  }
  .section__right__social {
    margin-top: -40px;
  }
}

@media (max-width: 437px) {
  .section__left {
    width: 80%;
  }
  .section__right__box {
    width: 55%;
  }
}
