.writers {
  background-color: #24478f;
  margin-top: 30px;
  padding: 0px 30px;
  padding-bottom: 70px;
  margin-left: 30px;
  margin-right: 30px;
  border-radius: 15px;
  margin-bottom: 60px;
  box-shadow: 3px 3px 7px rgb(202, 201, 201);
}
.writers h3 {
  font-size: 30px;
  text-align: center;
  padding: 20px;
  padding-top: 40px;
  color: white;
  letter-spacing: 1.5px;
}
.writers__allprofiles {
}
.writers__profiles {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

@media (max-width: 439px) {
  .writers {
    padding: 0px 5px;
    padding-bottom: 30px;
  }
}
