.profile {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 0px 60px;
  margin-top: 50px;
  margin-bottom: 50px;
}
.profile img {
  height: 95px;
  width: 95px;
  border-radius: 45px;
}
.profile p {
  color: white;
  font-size: 22px;
  margin-top: 12px;
  font-weight: 500;
  letter-spacing: 1.1px;
}
@media (min-width: 685px) and (max-width: 730px) {
  .profile p {
    font-size: 18px;
  }
  .profile img {
    height: 60px;
    width: 60px;
  }
}

@media (min-width: 607px) and (max-width: 684px) {
  .profile p {
    font-size: 15px;
  }
  .profile img {
    height: 53px;
    width: 53px;
  }
  .profile {
    margin: 50px 40px;
  }
}

@media (max-width: 606px) and (min-width: 500px) {
  .profile {
    margin: 50px 35px;
  }
  .profile p {
    font-size: 11px;
  }
  .profile img {
    height: 48px;
    width: 48px;
  }
}

@media (max-width: 500px) {
  .profile {
    margin: 44px 20px;
  }
  .profile p {
    font-size: 11px;
  }
  .profile img {
    height: 39px;
    width: 39px;
  }
}
